import * as Yup from "yup";
import axios from "../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import { Button, LinearProgress, Container, Paper } from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { connect } from "react-redux";
import { EditorState, convertToRaw } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import "../../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
const mapStateToProps = (state) => ({
  Dept_ID: state.Dept_ID,
  collegeID: state.collegeID,
});
class deptActivity extends Component {
  state = {
    editorState: EditorState.createEmpty(),
  };

  onEditorStateChange=(editorState) => {
    this.setState({
      editorState,
    });
  };

  render() {
    const { editorState } = this.state;
    
    function uploadImageCallBack(file) {
      return new Promise((resolve, reject) => {
         const reader = new FileReader(); // eslint-disable-line no-undef
         reader.onload = (e) => resolve({ data: { link: e.target.result } });
         reader.onerror = (e) => reject(e);
         reader.readAsDataURL(file);
      });
    }
    return (
      <Container fluid maxWidth="xl">
        <Paper>
          <div
            style={{ padding: "2rem", marginLeft: "4rem", marginRight: "2rem" }}
          >
            <h5 class="card-title">Department Activity</h5>
            <Formik
              initialValues={{
                Activity_name: "",
              }}
              validationSchema={Yup.object({
                Activity_name: Yup.string().required(
                  "Deprtment Activity Name Required "
                ),
              })}
              onSubmit={(values, { setSubmitting, resetForm }) => {
                setTimeout(() => {
                  setSubmitting(false);
                  values.Description = draftToHtml(
                    convertToRaw(editorState.getCurrentContent())
                  );
                  axios
                    .post(
                      "api/DeptActivity/" +
                        this.props.Dept_ID +
                        "/" +
                        this.props.collegeID,
                      JSON.stringify(values),
                      {
                        headers: {
                          // Overwrite Axios's automatically set Content-Type
                          "Content-Type": "application/json",
                          Accept: "application/json",
                        },
                      }
                    )
                    .then((res) => {
                      if (res.status === 200) {
                        alert("Data Saved in Database :", res.data.flag);
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                    });
                  resetForm();
                  this.setState({
                    editorState: "",
                  });
                }, 500);
              }}
            >
              {({ submitForm, isSubmitting, values }) => (
                <Form>
                  <Field
                    component={TextField}
                    name="Activity_name"
                    type="text"
                    label="Activity Title (Name) "
                    variant="outlined"
                    fullWidth
                  />
                  <Editor
                    editorState={editorState}
                    wrapperClassName="demo-wrapper"
                    editorClassName="demo-editor"
                    onEditorStateChange={this.onEditorStateChange}
                    editorStyle={{ border: "2px solid #d2d2d2", height: "70vh", backgroundColor: '#fff' }}
                    toolbar={{
                      image: {
                        uploadCallback: uploadImageCallBack,
                        previewImage: true,
                        inputAccept:
                          "image/gif,image/jpeg,image/jpg,image/png,image/svg",
                        alt: { present: false, mandatory: false },
                        defaultSize: {
                          height: "100%",
                          width: "100%",
                        },
                        colorPicker: {
                         
                          className: undefined,
                          component: undefined,
                          popupClassName: undefined,
                          colors: [
                            "rgb(97,189,109)",
                            "rgb(26,188,156)",
                            "rgb(84,172,210)",
                            "rgb(44,130,201)",
                            "rgb(147,101,184)",
                            "rgb(71,85,119)",
                            "rgb(204,204,204)",
                            "rgb(65,168,95)",
                            "rgb(0,168,133)",
                            "rgb(61,142,185)",
                            "rgb(41,105,176)",
                            "rgb(85,57,130)",
                            "rgb(40,50,78)",
                            "rgb(0,0,0)",
                            "rgb(247,218,100)",
                            "rgb(251,160,38)",
                            "rgb(235,107,86)",
                            "rgb(226,80,65)",
                            "rgb(163,143,132)",
                            "rgb(239,239,239)",
                            "rgb(255,255,255)",
                            "rgb(250,197,28)",
                            "rgb(243,121,52)",
                            "rgb(209,72,65)",
                            "rgb(184,49,47)",
                            "rgb(124,112,107)",
                            "rgb(209,213,216)",
                          ],
                        },
                      },
                    }}
                  />
                  {isSubmitting && <LinearProgress />}
                  <Button
                    variant="contained"
                    color="primary"
                    disabled={isSubmitting}
                    onClick={submitForm}
                  >
                    Submit
                  </Button>
                </Form>
              )}
            </Formik>
          </div>
        </Paper>
      </Container>
    );
  }
}
export default connect(mapStateToProps)(deptActivity);
